<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list 
            ref="tgList" 
            :listType="['List']" 
            :IsShowAction="true" 
            :showCheckBox="false" 
            ModuleName="Billing"
            SubModuleCode="AuditRecords" 
            IdentityColumn="COLUMN_ID" 
            :HeaderText="$t('AuditRecords')" 
            :ListData="ListingData"
            :HeaderData="Headers" 
            :widgets="widgets" 
            :HeaderButtons="Buttons" 
            :TotalRecords="TotalRecords"
            :RenderRowActionMethod="renderActions" 
            :ListDataCallBackFunction="FetchAuditRecords" 
            :SortExp="SortExp"
            :SortBy="SortBy" 
            :NorecordfoundText="$t('NoRecordfound')" 
            v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" 
            v-on:SortdataButtonClick="sortdata"
            :LegendArray="LegendArray" 
            v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:ActionButtonClick="actionButtonClick"
            :SearchFields="leftSearchFields"
            SearchPosition="top"
        >
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'COST'">
                    <span class="textalignright paddingright5">{{ formatCurrency(data.row.COST) }}</span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'TRANSMIT_DATE'">
                    <span v-if="data.row.TRANSMIT_DATE" :title="data.row.TRANSMIT_DATE">
                        {{ formatDate(data.row.TRANSMIT_DATE) }}
                    </span>
                    <span v-else>--</span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'DATEOFWORK'">
                    <span v-if="data.row.DATEOFWORK" :title="data.row.DATEOFWORK">
                        {{ formatDate(data.row.DATEOFWORK) }}
                    </span>
                    <span v-else>--</span>
                </template>
            </template>
        </tg-list>

        <!-- Edit Exception Modal -->
        <transition name="modal">
            <div v-if="IsEditException" class="modal-mask">
                <div class="modal-wrapper">
                    <div class="modal-container modal-lg">
                        <div class="modal-header">
                            <h4>{{ $t("Exception") }}</h4>
                            <button type="button" class="close" @click="CloseEditException">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div class="modal-body">
                            <EditException 
                                v-if="IsEditException"
                                :columnId="selectedColumnId"
                                :clientId="selectedClientId"
                                :requestFrom="'AUDIT'"
                                @close="CloseEditException"
                                @saved="onExceptionSaved"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </section>
</template>

<script>
import { locateBillingService } from "../../services/locateBillingService";
import Schema from "./AuditRecordsSchema";
import EditException from "../BillingValidationData/components/EditException.vue";

export default {
    name: "AuditRecords",
    components: {
        EditException
    },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalRecords: 0,
            ListingData: [],
            Buttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: Schema.headerSchema,
            searchCondition: "",
            selectedId: null,
            IsEditException: false,
            selectedColumnId: null,
            selectedClientId: null
        }
    },
    created: function () {
        var vm = this;
        vm.Headers.forEach(element => {
            element.DISPLAY_NAME = vm.$t(element.DISPLAY_NAME);
        });
        Schema.bindleftCommonSearchdropdown(this);
        
        // Set up client selection change handler
        vm.leftSearchFields.find(f => f.fieldName === 'ClientName').onChange = this.onSelectionChange;
        
        // Set default date range to Today
        vm.setDefaultDateRange();
        
        // Fetch initial data
        vm.FetchAuditRecords();
    },
    methods: {
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    this.searchCondition = value.searchConditionJson;
                } else {
                    this.searchCondition = value.searchCondition || "";
                }
            } else {
                this.searchCondition = "";
            }
            
            // Transform date range parameters
            if (this.searchCondition) {
                this.searchCondition = this.searchCondition.replace(/InvoiceDate=([^&]+)/, function (_, val) {
                    let [from, to] = val.split("|");
                    return `DateFrom=${from}&DateTo=${to}`;
                });
            }
            
            this.FetchAuditRecords();
        },
        onSelectionChange(value, x) {
            var vm = this;
            vm.isLoading = true;
            vm.selectedId = x;
            var url = `clientId=${x}&configId=&reqFrom=&clientIds=`;
            
            locateBillingService.getUtilityList(url).then(response => {
                vm.isLoading = false;
                let SelectedUtilityList = [];
                var UtilityList = response.data;
                if (UtilityList != null && UtilityList.length > 0) {
                    UtilityList.forEach(item => {
                        let obj = {
                            name: item.UTILITY_NAME,
                            value: item.UTILITY_NAME
                        };
                        SelectedUtilityList.push(obj);
                    });
                    vm.leftSearchFields.find(f => f.fieldName === 'UtilityName').listOptions = SelectedUtilityList;
                } else {
                    vm.leftSearchFields.find(f => f.fieldName === 'UtilityName').listOptions = [];
                }
            }).catch(error => {
                vm.isLoading = false;
                vm.leftSearchFields.find(f => f.fieldName === 'UtilityName').listOptions = [];
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchAuditRecords();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'EditRecord':
                    this.editRecord(item);
                    break;
            }
        },
        commonHeaderButtonClick: function (event) {
            // Handle header button clicks if needed
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            if (this.SortExp == "") {
                this.SortExp = "ASC";
            } else if (this.SortExp == "ASC") {
                this.SortExp = "DESC";
            } else {
                this.SortExp = "ASC";
            }
            this.FetchAuditRecords();
        },
        FetchAuditRecords: function () {
            var vm = this;
            vm.isLoading = true;
            
            // Get recordsToShow from search fields
            const recordsField = vm.leftSearchFields.find(f => f.fieldName === 'Records');
            const pageSize = recordsField ? (recordsField.value || 10) : 10;
            vm.PageSize = parseInt(pageSize);
            
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            
            // Add search condition if available
            if (vm.searchCondition) {
                url += `&${vm.searchCondition}`;
            }
            
            // TODO: Replace with actual API call when endpoint is ready
            // locateBillingService.GetAuditRecords(url).then((response) => {
            //     vm.isLoading = false;
            //     if (response.data && response.data.result && response.data.result.DATA && response.data.result.DATA.length > 0) {
            //         vm.ListingData = response.data.result.DATA;
            //         vm.ListingData.forEach((item) => {
            //             if (item.TRANSMIT_DATE) {
            //                 item.TRANSMIT_DATE = new Date(item.TRANSMIT_DATE);
            //             }
            //             if (item.DATEOFWORK) {
            //                 item.DATEOFWORK = new Date(item.DATEOFWORK);
            //             }
            //         });
            //         vm.TotalRecords = response.data.result.DATA[0].TOTAL_RECORDS || response.data.result.DATA[0].TOTALRECORDS || 0;
            //         vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
            //         vm.CurrentPage = vm.PageNumber;
            //     } else {
            //         vm.ListingData = [];
            //         vm.TotalRecords = 0;
            //     }
            // }).catch((error) => {
            //     vm.isLoading = false;
            //     vm.ListingData = [];
            //     vm.TotalRecords = 0;
            //     console.error('Error fetching audit records:', error);
            // });
            
            // Placeholder - remove when API is ready
            setTimeout(() => {
                vm.isLoading = false;
                vm.ListingData = [];
                vm.TotalRecords = 0;
            }, 100);
        },
        formatDate(dateString) {
            if (!dateString) return '';
            if (dateString instanceof Date) {
                return dateString.toLocaleDateString();
            }
            const date = new Date(dateString);
            if (isNaN(date.getTime())) return dateString;
            return date.toLocaleDateString();
        },
        formatCurrency(value) {
            if (!value && value !== 0) return '0.00';
            return parseFloat(value).toFixed(2);
        },
        editRecord(record) {
            this.selectedColumnId = record.COLUMN_ID;
            this.selectedClientId = record.CLIENT_ID;
            this.IsEditException = true;
        },
        CloseEditException() {
            this.IsEditException = false;
            this.selectedColumnId = null;
            this.selectedClientId = null;
        },
        onExceptionSaved() {
            this.CloseEditException();
            this.FetchAuditRecords();
        },
        setDefaultDateRange() {
            // Set default date range to Today
            // This will be handled by the date picker component
            // If needed, we can set initial values here
        }
    }
}
</script>

<style scoped>
.textalignright {
    text-align: right;
}
.paddingright5 {
    padding-right: 5px;
}

.modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: table;
    transition: opacity 0.3s ease;
}

.modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}

.modal-container {
    width: 90%;
    max-width: 1200px;
    margin: 0px auto;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    transition: all 0.3s ease;
    max-height: 90vh;
    overflow-y: auto;
}

.modal-lg {
    width: 90%;
    max-width: 1200px;
}

.modal-header {
    padding: 15px;
    border-bottom: 1px solid #e5e5e5;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.modal-header h4 {
    margin: 0;
}

.modal-body {
    padding: 20px;
}

.modal-footer {
    padding: 15px;
    border-top: 1px solid #e5e5e5;
    text-align: right;
}

.close {
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    opacity: 0.5;
}

.close:hover {
    opacity: 1;
}

.modal-enter {
    opacity: 0;
}

.modal-leave-active {
    opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
</style>

